import React, { useState, useEffect } from "react";
import { api } from "../../api/api";
import { Genre } from "./genre";

export const GenreCard = ({
  currentOtt,
  setcurrentOtt,
  recommendation_data,
  setrecommendation_data,
}) => {
  const [genres, setgenres] = useState([]);

  useEffect(() => {
    // console.log(pid);
    let url = "/recommendation/genre/";
    api
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        if (data) {
          setgenres(data);
        }
      });
    return () => {};
  }, []);

  return (
    <div className="genre_row">
      {Object.keys(genres).map((keyName, data) => {
        return (
          <Genre
            key={keyName}
            currentOtt={currentOtt}
            setcurrentOtt={setcurrentOtt}
            setrecommendation_data={setrecommendation_data}
            title={genres[keyName]}
            id={keyName}
          />
        );
      })}
    </div>
  );
};
