import React from "react";
import PropTypes from "prop-types";
import { api } from "../../api/api";
export const Genre = ({
  currentOtt,
  setcurrentOtt,
  recommendation_data,
  setrecommendation_data,
  id,
  title,
}) => {
  const handle_click = () => {
    let url = "/recommendation/genre/" + id + "/?limit=500";
    api
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        if (data) {
          if (data.length > 0) {
            setrecommendation_data(data);
          }
        }
      });
  };
  return (
    <button
      className="genre_card"
      onClick={() => {
        handle_click();
      }}
    >
      {title}
    </button>
  );
};
